













































import Vue from 'vue';
import WorkExperience from '@/components/professional/WorkExperience.vue';
import Education from '@/components/professional/Education.vue';
import myData from '@/assets/data/professional.json';

export default Vue.extend({
  methods: {
    getId(elementType: String, index: Number): string {
      return `${elementType}_${index}`;
    },
    getSelector(elementType: String, index: Number): string {
      return `#${this.getId(elementType, index)}`;
    },
  },
  computed: {
    isDense() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        default:
          return false;
      }
    },
  },
  components: {
    WorkExperience,
    Education,
  },
  data() {
    return {
      myData,
    };
  },
});
